import React, { useState } from "react";
import { Layout } from "../../components/Layout";
import { showLoading, hideLoading } from "../../redux/features/alertSlice";
import { useDispatch } from "react-redux";
import axios from "axios";
import { toast } from "react-toastify";
const AddClub = () => {
  const dispatch = useDispatch();
  const [form, setForm] = useState({
    name: "",
    clubId: "",
    clubType: "",
    img: ["", "", "", ""], // 4 fixed image URL fields
    clubInfo: "",
    resources: "",
    date: "",
    applicationForm: "",
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm({
      ...form,
      [name]: value,
    });
  };

  const handleImageChange = (index, value) => {
    const updatedImages = [...form.img];
    updatedImages[index] = value;
    setForm({
      ...form,
      img: updatedImages,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      dispatch(showLoading());
      const res = await axios.post(
        "/api/v1/admin/add-club",
        {
          name: form.name,
          clubId: form.clubId,
          clubType: form.clubType,
          img: form.img,
          clubInfo: form.clubInfo,
          resources: form.resources,
          date: form.date,
          applicationForm: form.applicationForm,

          // could have used spread operator here {...form}
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        }
      );
      dispatch(hideLoading());
      if (res.data.success) {
        toast.success(res.data.message);
      }
    } catch (error) {
      dispatch(hideLoading());
      console.log(error);
    }
  };

  return (
    <Layout>
      <div className="min-h-screen flex items-center justify-center bg-gray-900">
        <div className="bg-gray-800 p-8 rounded-lg shadow-lg w-full max-w-lg">
          <h2 className="text-2xl font-bold mb-6 text-white text-center">
            Add New Club
          </h2>
          <form onSubmit={handleSubmit}>
            <div className="mb-4">
              <label
                htmlFor="clubId"
                className="block text-gray-400 font-semibold mb-2"
              >
                Club ID
              </label>
              <input
                type="text"
                id="clubId"
                name="clubId"
                value={form.clubId}
                onChange={handleChange}
                className="w-full px-3 py-2 border border-gray-600 rounded-lg bg-gray-700 text-white focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              />
            </div>
            <div className="mb-4">
              <label
                htmlFor="name"
                className="block text-gray-400 font-semibold mb-2"
              >
                Club Name
              </label>
              <input
                type="text"
                id="name"
                name="name"
                value={form.name}
                onChange={handleChange}
                className="w-full px-3 py-2 border border-gray-600 rounded-lg bg-gray-700 text-white focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              />
            </div>
            <div className="mb-4">
              <label
                htmlFor="clubType"
                className="block text-gray-400 font-semibold mb-2"
              >
                Club Type
              </label>
              <input
                type="text"
                id="clubType"
                name="clubType"
                value={form.clubType}
                onChange={handleChange}
                className="w-full px-3 py-2 border border-gray-600 rounded-lg bg-gray-700 text-white focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              />
            </div>
            {form.img.map((image, index) => (
              <div className="mb-4" key={index}>
                <label
                  htmlFor={`img-${index}`}
                  className="block text-gray-400 font-semibold mb-2"
                >
                  Image URL {index + 1}
                </label>
                <input
                  type="text"
                  id={`img-${index}`}
                  name={`img-${index}`}
                  value={image}
                  onChange={(e) => handleImageChange(index, e.target.value)}
                  className="w-full px-3 py-2 border border-gray-600 rounded-lg bg-gray-700 text-white focus:outline-none focus:ring-2 focus:ring-indigo-500"
                  required
                />
              </div>
            ))}
            <div className="mb-4">
              <label
                htmlFor="clubInfo"
                className="block text-gray-400 font-semibold mb-2"
              >
                Club Information
              </label>
              <textarea
                id="clubInfo"
                name="clubInfo"
                value={form.clubInfo}
                onChange={handleChange}
                className="w-full px-3 py-2 border border-gray-600 rounded-lg bg-gray-700 text-white focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              ></textarea>
            </div>
            <div className="mb-4">
              <label
                htmlFor="applicationForm"
                className="block text-gray-400 font-semibold mb-2"
              >
                Application Form
              </label>
              <textarea
                id="applicationForm"
                name="applicationForm"
                value={form.applicationForm}
                onChange={handleChange}
                className="w-full px-3 py-2 border border-gray-600 rounded-lg bg-gray-700 text-white focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              ></textarea>
            </div>
            <div className="mb-4">
              <label
                htmlFor="resources"
                className="block text-gray-400 font-semibold mb-2"
              >
                Resources
              </label>
              <textarea
                id="resources"
                name="resources"
                value={form.resources}
                onChange={handleChange}
                className="w-full px-3 py-2 border border-gray-600 rounded-lg bg-gray-700 text-white focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              ></textarea>
            </div>
            <div className="mb-4">
              <label
                htmlFor="date"
                className="block text-gray-400 font-semibold mb-2"
              >
                Established Date
              </label>
              <input
                type="date"
                id="date"
                name="date"
                value={form.date}
                onChange={handleChange}
                className="w-full px-3 py-2 border border-gray-600 rounded-lg bg-gray-700 text-white focus:outline-none focus:ring-2 focus:ring-indigo-500"
                required
              />
            </div>
            <div className="mb-4">
              <button
                type="submit"
                className="w-full bg-indigo-600 text-white py-2 rounded-lg font-semibold hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 transition duration-300"
              >
                Add Club
              </button>
            </div>
          </form>
        </div>
      </div>
    </Layout>
  );
};

export default AddClub;
